#theBestPlayers {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    font-weight: 500;
    font-size: 20px;
    padding: 1em;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .25), 4px 4px 40px rgba(255, 0, 0, .9), inset 4px 5px 4px rgba(0, 0, 0, .5);
}

.rankingTitle {
    display: inline;
    grid-column: span 3;
    margin: 1em;
    text-align: center;
    font-weight: 600;
    font-size: 2rem;
}

#theBestPlayers>.header {
    font-weight: 700;
}

#theBestPlayers>p {
    margin: 5px;
}

@media screen and (max-width: 1000px) {
    #theBestPlayers {
        padding: 5px;
        min-height: 155px;
    }

    #theBestPlayers>* {
        font-size: 1rem;
    }

    .rankingTitle {
        font-size: 1rem;
        margin: 0;
    }

}